
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import ShowMore from '@/components/ShowMore/showMore'
export default{
data(){
return {
objData: {}
}
},
mounted(){
this.initInfo();
},
methods:{
getString(info){
return typeof info !='undefined' ? info : ''
},
initInfo(){

this.$ajax.post('',{
'MSH.1':'bornCertificationService',
'MSH.2':'getById',
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(data => {
if(typeof data !='undefined'){
this.objData = data;
}
}).catch(error => {
console.log(error);
});

}

},
components: {
XHeader,Collapse,ShowMore
}
}




